var MasterBus                 = require('./MasterBus');
var rateLimitedAudioListener  = require('./rateLimitedListener');

var StereoAmbience = function(audioContext){
  this.$listener = rateLimitedAudioListener;
  this.isPlaying = false;
};

StereoAmbience.load = function( assets, onDone ){

  this._instance.load( assets ).then( function(){
    onDone && onDone( this._instance );
    return this._instance;
  });

};
StereoAmbience.getInstance = function() {
  if (!this._instance) {
    this._instance = new StereoAmbience(THREE.AudioContext.getContext());
    this._instance.setupAudio();
  }
  return this._instance;
},
StereoAmbience.prototype.play = function(when, fadeTime){

  fadeTime = fadeTime || 0;
  if (this.isPlaying) return;

  if (this.roomAmbience && this.rainAmbience) {

    if (fadeTime) {
      this.roomAmbience.gain.gain.setValueAtTime(0, THREE.AudioContext.getContext().currentTime + when);
      this.roomAmbience.gain.gain.linearRampToValueAtTime(1, THREE.AudioContext.getContext().currentTime + when + fadeTime);
      this.rainAmbience.gain.gain.setValueAtTime(0, THREE.AudioContext.getContext().currentTime + when);
      this.rainAmbience.gain.gain.linearRampToValueAtTime(1, THREE.AudioContext.getContext().currentTime + when + fadeTime);
    }
    this.roomAmbience.play(when);
    this.rainAmbience.play(when);
    this.isPlaying = true;

  }
};

StereoAmbience.prototype.stop = function(when, fadeTime){

  when = when || 0;
  fadeTime = fadeTime || 0;

  if (this.roomAmbience && this.rainAmbience) {
    this.roomAmbience.stop(THREE.AudioContext.getContext().currentTime + when + fadeTime);
    this.rainAmbience.stop(THREE.AudioContext.getContext().currentTime + when + fadeTime);
  }
  this.isPlaying = false;
};

StereoAmbience.prototype.loadSingle = function( loader, url ){
  var _this = this;
  return new Promise(function(resolve, reject){

    loader.load(url, function(buffer) {
      _this.buffers[url] = buffer;
      resolve();
    })
  });
};

StereoAmbience.prototype.fadeVolume = function( volume, duration ){
  this.rainAmbience.gain.gain.linearRampToValueAtTime(volume, THREE.AudioContext.getContext().currentTime + duration);
};

StereoAmbience.prototype.setVolume = function(intensity, averageDropTime) {

  averageDropTime = averageDropTime || 0;

  this.rainAmbience.gain.gain.setValueAtTime(intensity * 0.6, THREE.AudioContext.getContext().currentTime + averageDropTime);

};

StereoAmbience.prototype.load = function(assets) {

  var urls = assets.separateChannels;
  var loader = new THREE.AudioLoader();
  var promises = [];

  for ( var i = 0; i < urls.length; i++ ){
    promises.push( this.loadSingle( loader, assets.uriPrefix + urls[i] ) );
  }
  return Promise.all( promises );
};


StereoAmbience.prototype.setupBuffers =  function( assets, delay ) {
  var _this = this;

  setTimeout(function(){

    for (var i = 0; i < assets.ambience_audio.separateChannels.length; i++) {

      var buffer = _this.buffers[assets.ambience_audio.uriPrefix + assets.ambience_audio.separateChannels[i]];

      if (i == 0) {
        _this.roomAmbience.setBuffer(buffer);
      }else {
        _this.rainAmbience.setBuffer(buffer);
      }
    }

    _this.play();
  }, delay * 1000);


}
StereoAmbience.prototype.setupAudio =  function() {
  this.buffers = {};

  for (var i = 0; i < 2; i++) {


    var soundSettings = {
      autoPlay: false,
      loop: true,
      vol: i == 0 ? 0.03 : 0,
    };
    if (i == 0) {
      this.roomAmbience = this.createAudio( soundSettings );
    }else {
      this.rainAmbience = this.createAudio( soundSettings );
    }

  }
};

StereoAmbience.prototype.createAudio =  function( soundSettings ) {

  var sound;

  sound = new THREE.Audio( this.$listener );

  sound.setPlaybackRate( soundSettings.playbackrate ||1 );
  console.log(soundSettings.vol);
  sound.setVolume( soundSettings.vol != undefined ? soundSettings.vol :0 );
  sound.setLoop( soundSettings.loop != undefined ? soundSettings.loop :  false);
  if (soundSettings.autoPlay) {
    sound.play();
    if (soundSettings.fadeIn) {
      sound.gain.gain.setValueAtTime(0, THREE.AudioContext.getContext().currentTime);
      sound.gain.gain.linearRampToValueAtTime(soundSettings.vol, THREE.AudioContext.getContext().currentTime + soundSettings.fadeIn);
    }
  }
  return sound;
};


module.exports = StereoAmbience;
