/******************************************************************************
* Main Scene
******************************************************************************/


var TWEEN                     = require('tween.js/src/Tween.js');
var screenShot                =  require('../helpers/screenShot');

// Framework
var Reticle                   = require('framework/UI/Reticle');
var VRScene                   = require('framework/VRScene');
var VRControl                 = require('three/examples/js/controls/VRControls');
var device                    = require('framework/helpers/device');
var masterBus                 = require('../audio/masterBus');
var WakeLock                  = require('framework/libs/wakelock/wakelock');
var Button                    = require('framework/UI/Button');
var util                      = require('framework/util');

require('framework/libs/three/controls/VRGamePad');

// Objects
var umbrella                  = require('../objects/umbrellaLowPoly');
var rain                      = require('../objects/rain');
var soundPool                 = require('../objects/soundPool');
var rainAutomation            = require('../objects/rainAutomation');
var umbrellaToggleButton      = require('../objects/umbrellaToggleButton');
var thunder                   = require('../objects/thunder');
var floor                     = require('../objects/floor');
var voSpeaker                 = require('../objects/voSpeaker');
var cameraRig                 = require('../objects/cameraRig');
var restartButton             = require('../objects/restartButton');
var titleTexture              = require('../objects/titleTexture');

// Helpers
var instructions              = require('../helpers/instructions');
var MouseRaycaster            = require('../helpers/MouseRaycaster');
var toggleFullScreen          = require('../helpers/toggleFullScreen');
var events                    = require('../helpers/events');
var getWindowOrientation      = require('../helpers/getWindowOrientation');

// Audio
var soundpacks                = require('../audio/soundpacks/index');
var sharedSounds              = require('../audio/sharedSounds');

// Settings
var wind                      = require('../settings/wind');
var timing                    = require('../settings/timing');
var globalSettings            = require('../settings/globalSettings');

// DOM Elements
var loadScreen                = require('../dom/loadScreen');
var landingScreen             = require('../dom/landingScreen');
var aboutScreen               = require('../dom/aboutScreen');

var wakelock;
if ( device.isMobile ){
  wakelock = new WakeLock();
}

var settings = {
    soundPack: soundpacks[ 0 ],
    size: 0.01,
    rainIntensity: 0,
    cloudRange: 20
};

var SKIP_INSTRUCTIONS = !!util.getParameterByName('skip_instructions');
var START_IN_VR = !!util.getParameterByName('vr');

var webvrAvailible = (function () {
  return navigator.getVRDisplays !== undefined || navigator.getVRDevices !== undefined;
}());

var mainScene = VRScene.create({

  onLoad: function(){

    loadScreen.show();

    var promises = [
      soundpacks[ 0 ].load(),
      sharedSounds.load(),
      rain.load(),
      umbrella.load(),
      thunder.load(),
      floor.load(),
      voSpeaker.load(),
      titleTexture.load(),
      restartButton.load(),
    ];

    // default to false
    this.isWebVRAvailible = false;

    // not really loading, but resolve the WebVR check before we start.
    if ( webvrAvailible ){
      promises.push( navigator.getVRDisplays().then(function(displays){
        this.isWebVRAvailible = displays.length > 0 && displays[0].displayName !== 'Mouse and Keyboard VRDisplay (webvr-polyfill)' ;
      }.bind(this)));
    }

    return Promise.all( promises );

  },

  // event handlers will be bound to <this> on setup
  eventHandlers: {

    onChangeSoundPack: function( e ) {
      var name = e.data;
      if ( name == this.currentSoundPackName ) return;
      this.currentSoundPackName = name;
      var soundPack = soundpacks.find( function( el ){
        return el.name === name;
      });
      this.setupSoundPack( soundPack );
    },

    onRainAnimationChange: function( e ){
      var rainIntensity = e.data;
      if (this.currentSoundPack ) {
        var vol = rainIntensity  / 100;
        if ( rainIntensity === 1 ) vol = 0;
        this.currentSoundPack.audioSource.onRainIntencityChange(vol, rain.averageDropTime);
      }
    },

    onRainComplete: function( e ) {
      instructions.startEnding(function(){
        this.endExperience();
      }.bind(this));
    },

    onOrientationChange: function(){
      switch(getWindowOrientation())
      {
        case -90:
        case 90:
          //landscape
          this.unpause();

          break;
        default:
          //portrait
          this.pause();

          break;
      }
    },
  },

  setup: function( $scene ) {

    window.screenShot = function(){
      screenShot( this.$renderer, this.$camera, $scene );
    }.bind(this);

    thunder.setup( $scene );
    voSpeaker.setup( $scene );
    titleTexture.setup( $scene );
    restartButton.setup( $scene );

    this.setupEndingScreen();
    rainAutomation.rainIntensity = settings.rainIntensity;

    this.currentSoundPackName = soundpacks[0].name;

    if ( soundpacks[ 0 ].audioSource ) {
      soundpacks[ 0 ].audioSource.addToScene( this.$scene );
    }

    //audio element needs to be played on user event on android. Started in onTouchEnd()
    if (!device.isAndroid) {
      this.changeAmbience( soundpacks[ 0 ] );
    }

    //start to load next soundpack
    var nextId = this.getNextSoundPackName();
    soundpacks[ nextId ].load();

    $scene.fog = new THREE.Fog( 0x000000, 3, 35 );
    $scene.background = new THREE.Color( 0x000000 );
    var fogColor = {color: $scene.fog.color.getHex() }; // #1e2e57


    this.mouseRayCaster = new MouseRaycaster( cameraRig.$camera );


    this.setupLanding();
    this.setupAbout();

    masterBus.setup();

    sharedSounds.setup();

    soundPool.setup();
    soundPool.mute();
    soundPool.createSounds( soundpacks[ 0 ], this.$scene );

    events.addEventListener(events.RESTART_EXPERIENCE,function(){
      this.restartExperience();
    }.bind(this),false);
    this.mouseRayCaster.addCollider( restartButton.$transform );

    rain.setup( $scene, settings.size, settings.transparent,
        settings.opacity,
        settings.color, settings.cloudRange, settings.rainIntensity );


    umbrella.setup( $scene );

    this.setupLights( $scene );

    //remove loading div
    loadScreen.fadeOut(function(){
      this.showLanding();
    }.bind( this) );

    rainAutomation.setup($scene );

  },

  setupVRControllers: function(){

    if (!navigator.getGamepads) return;

    this.vrGamePad = new THREE.VRGamePad( 0 );

    this.vrGamePad.addEventListener('buttondown',function( e ){
      umbrella.toggle();
    }.bind(this));


    if ( this.vrGamePad ){
      console.log('vrController',this.vrGamePad);
      this.$scene.add(this.vrGamePad);
    }
  },

  startRain: function(){
    if (!SKIP_INSTRUCTIONS){
      voSpeaker.transitionIn( timing.speakerWinchTime, function(){
          instructions.start( function(){
            voSpeaker.startListeningToSoundChange();
            rainAutomation.start();
          });

      }.bind( this ));
    } else {
      voSpeaker.transitionIn( 10 );
      voSpeaker.startListeningToSoundChange();
      rainAutomation.start();
    }
  },

  restartExperience: function(){

    var scene = this.$scene;
    var reticle = this.reticle;

    setTimeout(function(){
      umbrella.addToScene();
      floor.addToScene();

      scene.background.setHex( 0x000000 );
      scene.fog.color.copy( scene.background );

      scene.add( voSpeaker.$transform );


      rainAutomation.handleRestart();
      this._experienceStarted = false;
      SKIP_INSTRUCTIONS = true;
      this.startRain();
      scene.remove(this.grid);

      if (reticle) {
        reticle.remove_collider( restartButton.sprite );
      }

    }.bind(this), 3000);

    sharedSounds.play('kick_out');
    scene.remove( this.endingScreen );

  },

  startExperience: function(){
    //cameraRig.tweenPosition({ x:0, y:1.6, z:2}, 1000 );
    cameraRig.tweenRotation( { x: 0, y: 0, z: 0 }, 1000 );

    rain.fadeOutIntroRain(function(){

      floor.setup( this.$scene );


      if ( this._experienceStarted ){
        console.error('startExperience called twice')
        return;
      }

      this._experienceStarted = true;

      events.addEventListener( events.TOGGLE_UMBRELLA, this.onUmbrellaStateChange.bind( this ) );

      this.startRain();


      // initial rain change, starts the rain
      rainAutomation.changeRain( true );
      rainAutomation.addEventListener( 'SOUND_PACK_CHANGE', this.eventHandlers.onChangeSoundPack );
      rainAutomation.addEventListener( 'RAIN_ANIMATION_CHANGE', this.eventHandlers.onRainAnimationChange );
      rainAutomation.addEventListener( 'COMPLETE', this.eventHandlers.onRainComplete );

      if ( wakelock ){
        wakelock.request();
      }

      var tween = cameraRig.tweenPosition( { x:0, y: 1.6, z: 0 }, 1000 ).delay( 800 );
      umbrella.toggle(umbrella.DOWN);

      tween.onComplete(function(){

        this.$controls = new THREE.VRControls( cameraRig.$camera );
        this.$controls.standing = true;
        this.$controls.resetPose();


        umbrella.followCamera( true );

        //hide splash div
        if (device.isMobile) {
          var _this = this;
          setTimeout( function(){

            window.addEventListener( 'orientationchange', _this.eventHandlers.onOrientationChange, true );
            _this.eventHandlers.onOrientationChange();
          }, 1000 );

        }

        cameraRig.startExperience();
        soundPool.unMute();

      }.bind(this));


      if ( !device.isMobile ) {
        document.body.addEventListener( 'keyup', function( e ){
            if( e.keyCode === 32 ){
                umbrella.toggle();
            } else if ( e.keyCode === 27 ){
              this.toggleVR();
            }
        }.bind(this), false );
      } else {
        document.addEventListener('touchstart', function() {
          umbrella.toggle();
        });
      }
      if ( device.isMobile ) {
        this.$scene.add( umbrellaToggleButton );
        umbrellaToggleButton.position.z = -1;
        umbrellaToggleButton.position.y = 0.01;
        umbrellaToggleButton.rotation.x = -0.5 * Math.PI;
        this.mouseRayCaster.addCollider( umbrellaToggleButton );

        this.initReticle();
      }

      rain.startExperience();

    }.bind(this));

  },

  initReticle: function(){
    if ( !this.reticle ){
      this.reticle = new Reticle(cameraRig.$camera);
      if ( device.isMobile ) {
        this.reticle.add_collider( umbrellaToggleButton.mesh );
      }
    }
  },

  destroyReticle: function(){
    this.reticle = null;
  },

  setupLights: function($scene){
    var dirLight = new THREE.DirectionalLight( 0xFFFFFF, 1.3 );
    dirLight.position.set(-13, 10, 20);
    $scene.add( dirLight );

    // var dirlighthelper = new THREE.DirectionalLightHelper( dirLight );
    // $scene.add( dirlighthelper );


    var dirLight2 = new THREE.DirectionalLight( 0xFFFFFF, 1.0 );
    dirLight2.position.set(13, 2, -10);
    $scene.add( dirLight2 );

    this.dirLight2 = dirLight2;

    var ambLight = new THREE.AmbientLight( 0xFFFFFF, 0.39 );
    $scene.add( ambLight );
    globalSettings.gui.add(ambLight,'intensity', 0, 2 ).name('AmbInten');
  },

  setupCamera: function() {
    cameraRig.setup();
    return cameraRig.$camera;
  },

  setupSoundPack: function(soundPack){
    var _this = this;
    soundPack.load().then( function(){
      soundPool.setBuffers(soundPack);
      _this.changeAmbience(soundPack);

      rainAutomation.start();
      var nextId = _this.getNextSoundPackName();
      soundpacks[ nextId ].load();
    });
  },

  getNextSoundPackName: function() {

    var id = 0;

    for (var i = 0; i < soundpacks.length; i++) {
      if ( soundpacks[i].name === this.currentSoundPackName ) {
        id = i;
      }
    }
    return ( id + 1 ) % soundpacks.length;
  },

  changeAmbience: function(soundPack) {

    var isFirstTime = false;

    if (this.currentSoundPack ) {
      this.currentSoundPack.audioSource.stop( rain.averageDropTime );
    } else {
      isFirstTime = true;
    }

    soundPack.audioSource.changeAmbience(soundPack.assets, isFirstTime ? 0 : rain.averageDropTime);

    this.currentSoundPack = soundPack;
  },

  onTouchEnd: function() {
    if (device.isAndroid) {
      this.changeAmbience( soundpacks[ 0 ] );
    }
  },

  setupAbout: function() {

    var _this = this;
    document.getElementById('about-button').addEventListener('click', function() {
      aboutScreen.show();
      masterBus.setVolume(0.2, 0.5);
      //_this.hideVRButton();
    });

    document.getElementById( 'about-xbutton' ).addEventListener( 'click', function() {
      aboutScreen.hide();
      masterBus.setVolume(1, 0.5);
      //_this.showVRButton();
    });

  },

  exitVR: function(){

    if ( this._isInVR ){
      // this.destroyReticle();
      // this.VREffect.exitPresent();
      // this._isInVR = false;
      this.showLanding();
    }

  },

  enterVR: function(){

    // if ( !this._isInVR ){
    //
    //   if ( device.isMobile ) {
    //     this.initReticle();
    //   }
    //
      this.hideLanding();

    //   this.VREffect.requestPresent().catch(function(e){
    //     console.error(e);
    //   }.bind(this));
    //
    //   this.showLanding();
    //   this._isInVR = true;
    //   this.setupVRControllers();
    //
    // }

    document.getElementsByClassName('plan8-logo-container')[0].style.display = 'none';
    document.getElementsByClassName('webvr-logo-container')[0].style.display = 'none';
  },

  toggleVR: function(){
    if ( this._isInVR ){
      this.exitVR();
    } else {
      this.enterVR();
    }
  },

  showNoVRLanding: function(){
    landingScreen.show( false );
  },

  showLanding: function(){
    landingScreen.show( true );
  },

  hideLanding: function(){
    landingScreen.fadeOut();
  },

  setupEndingScreen: function() {
    this.endingScreen = new THREE.Object3D();
    this.endingScreen.position.z = -3;
    this.endingScreen.position.y = 0.4;
    restartButton.$transform.position.y = 1.6;
    this.endingScreen.add( restartButton.$transform );
    this.endingScreen.add( titleTexture.$transform );
  },

  endExperience: function(){
    var scene = this.$scene;
    setTimeout(function(){

      if (device.isMobile && this.reticle) {
        this.reticle.add_collider( restartButton.sprite );
      }

      umbrella.removeFromScene();
      floor.removeFromScene();

      this.$scene.remove(voSpeaker.$transform);
      this.grid = new THREE.GridHelper(50,100);
      this.grid.material.fog = true;
      scene.background = new THREE.Color(0.6, 0.6, 0.6);
      scene.fog.color.copy( scene.background );
      scene.add(this.grid);

      var orgY = this.endingScreen.position.y;

      this.endingScreen.position.set( 0, 0, - 3 );
      this.endingScreen.position.applyMatrix4( cameraRig.$camera.matrixWorld );

      this.endingScreen.position.y = orgY;
      this.endingScreen.lookAt( cameraRig.$camera.position );
      scene.add(this.endingScreen);

      voSpeaker.playLoungeLoop();

    }.bind( this ), 3000 );

    sharedSounds.play('kick_out');

  },

  setupLanding: function() {

    var _this = this;

    this.vrButton.on("enter", function(){
      setTimeout(function(){
        _this.startExperience();
        _this.setupVRControllers();
        _this.hideLanding();

      }, 800 );
    })
    .on("exit", function(){
        console.log("exit VR");
        _this.$camera.quaternion.set(0,0,0,1);
        _this.$camera.position.set(0,_this.$controls.userHeight,0);
    })
    .on("error", function(error){
        console.error(error)
    });

    if ( START_IN_VR ){
      this.vrButton.getVRDisplay().then(function(display){
        _this.vrButton.requestEnterVR(display);
      })
    }
  },

  pause: function(){
    this._paused = true;
  },

  unpause: function(){
    this._paused = false;
  },

  /**
  * Main update loop
  */
  update: function(dt, timestamp) {

    if ( this._paused ){
      return;
    }

    if( this.$controls ){
        this.$controls.update();
        if( this.vrGamePad ) this.vrGamePad.standingMatrix = this.$controls.getStandingMatrix();

    }

    if ( this.vrGamePad ){
      this.vrGamePad.update();
    }

    if (this.reticle) {
      this.reticle.update(dt, timestamp);
    }

    rain.update(dt, timestamp);
    umbrella.update(dt, timestamp);
    voSpeaker.update(dt, timestamp);
    instructions.update(dt, timestamp);

    // drive TWEEN.js animations
    TWEEN.update();
  },

  onUmbrellaStateChange: function( e ){
    sharedSounds.toggleUmbrella( e );
  },

});

module.exports = mainScene;
