var THREE = require('three');
var Text = require('./Text.js');

var TWO_PI = Math.PI * 2;

function Reticle( camera ){

  this.camera = camera;
  this.radius = 0.06;
  this.lineWidth = 0.01;
  var geometry = new THREE.RingGeometry( this.radius - this.lineWidth, this.radius, 64, 4, 0, Math.PI * 2 );
  var material = new THREE.MeshBasicMaterial( { color: 0xffffff, wireframe: false, side: THREE.BackSide } );
  var reticleMesh = new THREE.Mesh( geometry, material );

  var progressMesh = new THREE.Mesh( geometry.clone(), material.clone() );

  var textObject = new THREE.Object3D();
  // flip'em so that progress goes in the right direction
  reticleMesh.position.set(0, 0, -1.6);
  progressMesh.position.set(0,0,-1.6);
  progressMesh.rotation.set( 0, -Math.PI, 0 );
  textObject.position.set(0,0,-1.6);
  textObject.rotation.set( 0, -Math.PI, 0 );
  reticleMesh.rotation.set( 0, -Math.PI, 0 );

  this.reticleMesh = reticleMesh;
  this.progressMesh = progressMesh;
  this.textObject = textObject;

  this.camera = camera;

  camera.add(reticleMesh);
  camera.add(progressMesh);
  camera.add(textObject);

  //this._reticle.reticle_arm_object.add(reticleMesh);
  this.colliders = [];
  this.gazing_object = null;
  this.gazing_duration = 1000;
  this.gaze_release_duration = 0;
  this.gaze_long_timer = 0;
};

Reticle.prototype = {

  generateProgressMesh: function( progress ){

    this.progressMesh.geometry.dispose();
    this.progressMesh.geometry = new THREE.RingGeometry( this.radius + this.lineWidth, this.radius + this.lineWidth * 2, 64, 4, Math.PI * 0.5, TWO_PI * progress );
  },

  updateGazeProgress: function( progress ){
    progress = Math.max( 0, Math.min( progress, 1 ) );
    this.generateProgressMesh( progress );
  },

  update: function( dt, t ){
    //this._reticle.reticle_loop();
    this.hitDetection( dt, t );

  },
  hide: function() {
    this.camera.remove(this.reticleMesh);
    this.camera.remove(this.progressMesh);
  },
  show: function() {
    this.camera.add(this.reticleMesh);
    this.camera.add(this.progressMesh);
  },
  resetAnimation: function(){
    this.updateGazeProgress(  0 );
  },

  hitDetection: function( dt, t ){
    //hack, these values should be calculated
    var vector = new THREE.Vector3(-0.0012499999999999734, -0.0053859964093356805, 0.5);
    vector.unproject(this.camera);
    var ray = new THREE.Raycaster(this.camera.position, vector.sub(this.camera.position).normalize());
    var intersects = ray.intersectObjects(this.colliders);
    //if an object is hit
    if (intersects.length > 0) {
        //save the new hit object and time
        this.reticle_hit_object = intersects[0].object;
        this.reticle_hit_time = t;

        //is the hit object gazeable
        if (this.reticle_hit_object.gazeable) {

            this.isReleased = false;
            //this.reticle_object.material = this.get_random_hex_material();

            //check if there's a gazing object
            if (this.gazing_object != null) {
                //if the gazing object is the same as the hit object: check to see if the elapsed time exceeds the hover duration
                if (this.gazing_object === this.reticle_hit_object) {

                    //if it does: trigger the click
                    var progress = ( this.reticle_hit_time - this.gazing_time ) / this.gazing_duration;
                    this.updateGazeProgress( progress );

                    if ( progress >= 1 ) {
                        if (this.gazing_object.ongazelong != undefined) {
                            this.gazing_object.ongazelong();
                        }
                        //reset gazing time
                        //this.gazing_time = this.reticle_hit_time;
                        //this.resetAnimation();
                    }
                } else {
                    //if there is but it doesn't match the hit object: save the new hit object and time
                    //console.log("gaze out");
                    this.resetAnimation();
                    this.gazing_object = this.reticle_hit_object;
                    this.gazing_time = this.reticle_hit_time;
                    if (this.gazing_object.ongazeout != undefined) {
                        this.gazing_object.ongazeout();
                    }

                }

            } else {
                //if there is not: save the time and object as gazing
                //console.log("gaze over");
              //  this.shrink();
                this.gazing_object = this.reticle_hit_object;
                this.gazing_time = this.reticle_hit_time;
                if (this.gazing_object.ongazeover != undefined) {
                    this.gazing_object.ongazeover();
                }
            }

        }
    } else {
        if (this.gazing_object != null) {
            //console.log("gaze out");
            this.resetAnimation();
            if (this.gazing_object.ongazeout != undefined) {
                this.gazing_object.ongazeout();
            }
            //clear gazing and hit object and times
            this.reticle_hit_object = null;
            this.reticle_hit_time = null;
            this.gazing_object = null;
            this.gazing_time = null;
            this.gaze_out_time = t;
          //  this.reticle_object.material = this.default_material();
        }

        if ( !this.reticle_hit_object || !this.reticle_hit_object.gazeable ) {

          if ( this.gaze_release_duration > 0 ){
            var progress = ( this.gaze_out_time - t ) / this.gaze_release_duration;

            if ( progress >= -1 ){
              this.updateGazeProgress(  1+progress );

            } else if ( !this.isReleased ){
              this.ongazerelease && this.ongazerelease();
              this.isReleased  = true;
            }
          }
        }
    }
  },

  add_colliders: function( objects ){
    for(var i = 0; i < objects.length; i++ ){
      this.add_collider( objects[ i ] );
    }
    return this;
  },
  add_collider: function( threeObj ){
    threeObj.gazeable = true;
    this.colliders.push( threeObj );
    return this;
  },

  remove_collider: function( threeObj ){

      threeObj.gazeable = false;
      var index = this.colliders.indexOf(threeObj);

      if (index > -1) {
        this.colliders.splice(index, 1);
      }
  },

  showText: function( text, fontSize ){

    fontSize = fontSize || 60;

    if ( !( text instanceof Array ) ){
      text = [ text ];
    }

    if (this.textContainer ) {
      this.textObject.remove( this.textContainer  );
    }

    this.textContainer = new THREE.Object3D();

    var lines = text.length;

    for ( var i = 0; i < lines; i++ ){
      var txt = text[i];
      var textSprite;

      if ( typeof txt === 'object' ){
        textSprite = Text.makeTextMesh( txt.text, fontSize);
        textSprite.scale.multiplyScalar( txt.fontSize / fontSize );
      } else {
        textSprite = Text.makeTextMesh( txt, fontSize );
      }

      textSprite.position.y = 0.1+ -i / 2;
      this.textContainer.add( textSprite );

    }
    this.textContainer.rotation.y = -Math.PI;

    this.textObject.add( this.textContainer );

  },

  hideText: function(){
    var textContainer = this.textContainer;
    var _this = this;
    setTimeout( function(){
        _this.textObject.remove( textContainer );
    }, 300 );
  },
};

module.exports = Reticle;
