var sceneManager = {
  _scenes: [],
  loadScene: function( indexOrName ){
    var scene;
    if ( typeof indexOrName === 'number' ){
      scene = this._scenes[ indexOrName ];
    } else if ( typeof indexOrName === 'string' ){
      scene = this._scenes.find(function(s){
        return s.name === indexOrName;
      });
    }
    this._currentScene = scene;
    return scene.load();
  },
  register: function( scene ){
    this._scenes.push( scene );
    return this;
  }
};

Object.defineProperty( sceneManager, 'currentScene', {
  get: function(){
    return this._currentScene;
  }
});

module.exports = sceneManager;
