require( './webvrconfig' );
var webvrCheck = require('framework/helpers/webvrCheck');

var THREE = require('three');
window.THREE = THREE;
if ( !THREE.AudioContext.getContext ){
  THREE.AudioContext.getContext = function(){
    return THREE.AudioContext;
  }
}

//requered for some non-modulized plugins

require( 'webvr-polyfill' );

var mainScene   = require( './scenes/mainScene' );
var audioHelper = require( './helpers/audioHelper' );
var device      = require( 'framework/helpers/device' );

mainScene.load().then( function(){

  webvrCheck.check().then(function(){
    if ( device.isMobile ){
      document.getElementById('landing').style.visibility = 'visible';
      document.getElementById('viewInVRButton').addEventListener('touchend', function(e){
        window.scrollY=document.body.scrollHeight;
        audioHelper.initIOS();
        mainScene.handlers.onTouchEnd(); // starts omnitone on android
      }, false);
      mainScene.setup();
    } else {
      mainScene.setup();
    }
  });
});
