/******************************************************************************
* Gaze button to toggle umrella (up/down) on HMDs without buttons
******************************************************************************/

var Button = require( 'framework/UI/Button' );
var umbrella = require('./umbrellaLowPoly');
var events      = require('../helpers/events');

module.exports = new Button({
  type:"toggle",
  color1:0x505050,
  selectedColor:0xDDDDDD,
  id: 'umbrella_toggle',
  label: 'Toggle Umbrella',
  handleClick: function(){
    umbrella.toggle();
  }
});
